$tablet-breakpoint: 1024px;
$mobile-breakpoint: 767px;

$sidebar-margin-top: 0rem;

.wp-block-harvestworks-associated-events-list {

    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(18rem, 1fr));
    gap: 2.4rem;
    flex-wrap: wrap;


    @media (max-width: $mobile-breakpoint) {
        grid-template-columns: repeat(auto-fill, minmax(14rem, 1fr));
    }

    .event_card {
        padding: 1.20rem;

        display: flex;
        flex-direction: column;
        gap: 1rem;

        background-color: var(wp--preset--color--material-2);
        transition: background-color 0.3s;

        &:hover {
            background-color: var(--wp--preset--color--contrast-3);
        }

        text-decoration: none !important;

        img {
            aspect-ratio: 1;
            object-fit: cover;
            width: 100%;
        }

        .event_details {
            display: flex;
            flex-direction: column;
            height: 100%;
            padding-left: 0.2rem;
            padding-right: 0.2rem;

            overflow: hidden;

            .event_title {
                margin: 0rem;
                font-size: 1.4rem;
                overflow-wrap: break-word;
                font-weight: 600;
            }

            .event_date {
                margin: 0;
                margin-top: 0.2rem;
                margin-bottom: -0.2rem;
                color: var(--wp--preset--color--contrast-2);
                font-weight: 600;
                font-size: 1.2rem;
            }

            .event_tagline {
                margin: 0;
                padding-top: 0.6rem;

                font-size: 1.2rem;

                opacity: 0.45;
                font-weight: 600;
            }

            .event_excerpt {
                display: inline-block;

                margin: 0;
                margin-top: 0.6rem;
                font-size: 1.2rem;
                opacity: 1;
                font-weight: 400;

                position: relative;
                height: 0;
            }
        }

        &:has(img) .event_details .event_date {
            margin-top: 0rem;
        }

        &:has(img) .event_details .event_tagline {
            padding-top: 0.3rem;
        }

        &:has(img) .event_details .event_excerpt {
            display: none;
        }




    }
}